body {
  font-family: 'Mulish', sans-serif; }

.reviews {
  padding: 50px 65px;
  background-color: #262538;
  border-radius: 10px;
  flex-basis: 578px;
  max-width: 578px;
  display: flex;
  flex-direction: column;
  margin: auto; }
  .reviews .title {
    color: #FFFFFF;
    font-size: 22px;
    font-weight: bold;
    margin-bottom: 50px; }
  .reviews .search-tags {
    display: flex;
    flex-direction: column; }
    .reviews .search-tags .search-box {
      display: flex;
      align-items: center;
      position: relative;
      margin-bottom: 20px; }
      .reviews .search-tags .search-box input {
        width: 100%;
        background-color: #262538;
        color: #8C8AA7;
        border: 1px solid #49485D;
        border-radius: 5px;
        padding: 23px 28px 23px 65px;
        font-size: 18px; }
      .reviews .search-tags .search-box i {
        position: absolute;
        top: 23px;
        left: 28px;
        z-index: 1;
        width: 22px;
        height: 22px;
        display: flex;
        align-items: center;
        font-size: 18px; }
    .reviews .search-tags .tags {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 117px; }
      .reviews .search-tags .tags a {
        padding: 15px 25px;
        font-size: 14px;
        border-radius: 3px;
        color: #8C8AA7;
        background-color: #1D1C2D;
        margin: 5px; }
        .reviews .search-tags .tags a:hover {
          background-color: #8780F8;
          color: #FFFFFF;
          transition: 400ms all; }
  .reviews .card {
    display: flex;
    flex-direction: column;
    padding: 35px 37px;
    background-color: #2C2B3F;
    color: #FFFFFF;
    border-radius: 5px; }
    .reviews .card:not(:last-child) {
      margin-bottom: 10px; }
    .reviews .card-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 27px; }
      .reviews .card-header .user-info {
        display: flex;
        align-items: center; }
        .reviews .card-header .user-info .user-photo {
          width: 48px;
          height: 48px;
          background-color: #D56A6A;
          color: #FFFFFF;
          border-radius: 50%;
          display: flex;
          align-items: center;
          justify-content: center;
          margin-right: 10px; }
        .reviews .card-header .user-info .username {
          font-size: 18px;
          font-weight: bold;
          color: #FFFFFF; }
      .reviews .card-header .point {
        color: #D5A66A;
        font-size: 16px; }
    .reviews .card-content {
      display: flex;
      flex-direction: column;
      margin-bottom: 30px; }
      .reviews .card-content .discription {
        font-size: 18px; }
    .reviews .card-footer {
      display: flex;
      justify-content: space-between;
      align-items: center; }
      .reviews .card-footer .date {
        font-size: 18px;
        font-weight: 500;
        color: #8C8AA7; }
      .reviews .card-footer .btn-share {
        padding: 8px 15px 8px 15px;
        background-color: #2C2B3F;
        color: #979797;
        border: 1px solid #1F1E2D;
        border-radius: 3px; }
        .reviews .card-footer .btn-share i {
          margin-right: 8px; }

.row {
  display: flex;
  flex-direction: column; }

